.pageNav {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    overflow: hidden;
    width: 100%;
}

.pageNav > div > input {
    text-align: center;
}


.pageNav-button {
    background-color: transparent;
    color: $color-accent-green;
    padding: 0.5rem 1rem;
    margin: 0;
    font-size: 3.5rem;
}

.pageNav-button:hover {
    cursor: pointer;
}

#page {
    text-align: center;
    font-size: 2rem;
    border-radius: $border-radius;
    padding: 1rem 2rem;
    color: $color-text;
    background-color: transparent;
    border: 2px solid rgba($color-accent-green, 0.3);
    width: 100%;
}

.lp {
    background-color: rgba($color-accent-green, 0.7);
    border-bottom: 2px solid $color-text;

}

@media (max-width: 37.5em) {
    #page {
        font-size: 1.3rem;
        padding: 1rem 1rem;
    }
}