.Buy {
    background-color: rgba($color-accent-green, 0.7);
    border-top-left-radius: $border-radius;
    border-bottom-left-radius: $border-radius;
    z-index: 10000;

    &-full{
        background-color: rgba($color-accent-green, 0.7);
        z-index: 10000;
        border-radius: $border-radius;
    }
}

.Negativ {
    background-color: rgba($color-accent-purple, 0.5);
    border-top-right-radius: $border-radius;
    border-bottom-right-radius: $border-radius;
}
.Pink {
    background-color: rgba($color-accent-pink, 0.5);
}

.pressure .Buy, .pressure .Sell{
    white-space: nowrap;
}

.wide {
    width: 40%;
}

.table__subtext {
    color: #ccc;
    font-size: 1rem;
}

.transfer-volume td, .buy-sell-presure td {
    border-bottom: none;
}

