    overflow-y: scroll;
    text-align: center;

    @include respond(tab){
        margin: 0 0 2rem 0;
        width: 100%;
        height: 500px;
    }
}

.twitter-left {
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
    @include respond(tab){
        border-bottom-right-radius: 0;
        border-bottom-left-radius: 0;
        border-top-right-radius: $border-radius;
        border-top-left-radius: $border-radius;
        margin-bottom: 1.5rem;
    }

}
.twitter-right {
    border-top-left-radius: 0;
