.grid{
    display:grid;
    grid-template-columns: repeat(18, minmax(0, 1fr));
    grid-template-rows: repeat(18, minmax(0, 1fr));
    gap: 1rem 1rem;
    grid-auto-flow: row dense;
    padding: 1rem;
    @include respond(tab){
        grid-template-columns: minmax(0, 1fr);
        grid-template-rows: repeat(12, minmax(0, 1fr));

    }
    & > div{
        align-self: stretch;
        justify-items: stretch;
        border-radius: $border-radius;
        background-color: $color-base-module;
        padding: 1rem;

        & > div, & > a{
            z-index: 100;
        }
    }

    &-container{

        &-full{
            grid-column-end: span 18;
            grid-row-end: span 18;
            @include respond(tab){
                grid-column-end: span 1;
                grid-row-end: span 12;
            }

            &-but-short{
                grid-column-end: span 18;
                grid-row-end: span 12;
                @include respond(tab){
                    grid-column-end: span 1;
                    grid-row-end: span 12;
                }
            }
        }

        &-high{
            grid-column-end: span 6;
            grid-row-end: span 18;
            @include respond(tab){
                grid-column-end: span 1;
                grid-row-end: span 12;
            }
            &-thicc{
                grid-column-end: span 9;
                grid-row-end: span 18;
                @include respond(tab){
                    grid-column-end: span 1;
                    grid-row-end: span 12;
                }
            }
        }

        &-medium{
            grid-column-end: span 6;
            grid-row-end: span 6;
            @include respond(tab){
                grid-column-end: span 1;
                grid-row-end: span 4;
            }

            &-but-short{
                grid-column-end: span 6;
                grid-row-end: span 4;
                @include respond(tab){
                    grid-column-end: span 1;
                    grid-row-end: span 2;
                }
            }
        }

        &-small{
            grid-column-end: span 4;
            grid-row-end: span 6;
            @include respond(tab){
                grid-column-end: span 1;
                grid-row-end: span 2;
            }
        }

        &-wide{
            grid-column-end: span 12;
            grid-row-end: span 6;
            @include respond(tab){
                grid-column-end: span 1;
                grid-row-end: span 3;
            }

            &-and-high{
                grid-column-end: span 12;
                grid-row-end: span 18;
                @include respond(tab){
                    grid-column-end: span 1;
                    grid-row-end: span 12;
                }
            }

            &-but-short{
                grid-column-end: span 12;
                grid-row-end: span 4;
                @include respond(tab){
                    grid-column-end: span 1;
                    grid-row-end: span 12;
                }
            }
        }
    }
}