.toggle{
    display:-webkit-box;
    display:-ms-flexbox;
    display:flex;
    -webkit-box-align:center;
        -ms-flex-align:center;
            align-items:center;

    width: 5rem;
    height: 3rem;
    border-radius: $border-radius;
    background-color: $color-base;
    @include respond(tab){
        margin-top: 1rem;
        width: 7rem;
        height: 4rem;
    }
    @include respond(phone){
        width: 7rem;
        height: 3rem;
    }
    &::before{
        -webkit-box-pack: start;
            -ms-flex-pack: start;
                justify-content: start;
    }
    &:hover{
        cursor:pointer;
    }

    &__container{
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-align: center;
            -ms-flex-align: center;
                align-items: center;
        -webkit-box-pack: center;
            -ms-flex-pack: center;
                justify-content: center;
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
            -ms-flex-direction: column;
                flex-direction: column;
        color: $color-text;
        text-align: center;
    }

    &__ball{
        width: 2.5rem;
        height: 2.5rem;
        background-color: $color-accent-purple;
        border-radius: 50%;
        -webkit-transition: .5s;
        -o-transition: .5s;
        transition: .5s;
        position: absolute;

        @include respond(tab){
            width: 3.5rem;
            height: 3.5rem;
        }
        @include respond(phone){
            width: 3rem;
            height: 3rem;
        }
    }

    & > input[type="checkbox"]{
        display:none;
    
        &:checked + .toggle{
            background-color: $color-accent-purple;
        }
    
        &:checked + .toggle__ball{
            background-color: $color-accent-green;
            margin-left: 2.5rem;
            @include respond(tab){
                margin-left: 3.5rem;
            }
            @include respond(phone){
                margin-left: 4rem;
            }
        }
    }

    
}