.nav {
    &-button{
        border-radius: $border-radius;

        width: 5rem;
        height: 5rem;
        text-align: center;
        background-color: transparent;
        /* border: 2px solid rgba(0, 167, 157, 0.3); */
        padding: .5rem;
        color: #fff; 
        @include respond(tab){
            width: 7rem;
            height: 7rem;
        }
        @include respond(phone){
            width: 4rem;
            height: 4rem;
        }
        &:hover {
            background-color: rgba($color-accent-green, .5);

        }

        &:not(:last-child){
            margin-right: 1rem;
            @include respond(phone){
                margin-right: 0rem;

            }
        }

        & a{
            text-decoration: none;
            text-transform: uppercase;
        }

        & > img{
            width: 4rem;
            height: 4rem;
            @include respond(tab){
                width: 6rem;
                height: 6rem;
            }
            @include respond(phone){
                width: 3rem;
                height: 3rem;
            }
        }
    }
}

.choosen {
    background-color: rgba($color-accent-green, .8);
    /* border: 2px solid #00a79d; */
    color: #000000; 
}

.page > img{
    height: 4rem;
    margin-left: 3rem;
    @include respond(tab){
        height: 6rem;
    }
    @include respond(phone){
        height: 3rem;
        margin-left: 1rem;
    }
}