header, footer{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -ms-flex-item-align: stretch;
        -ms-grid-row-align: stretch;
        align-self: stretch;
    justify-items: stretch;
    border-radius: $border-radius;
    background-color: $color-base-module;
    padding: 1rem;
    margin: 0rem 1rem;


    & > a{
        margin-left: 0.5rem;
        margin-right: 0.5rem;
        @include respond(tab){
            margin: 2rem 1rem;
        }
        @include respond(phone){
            margin: 1rem .5rem;
        }
    }
}