input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

.tracker-element__input{
    background-color: $color-base-inner;
    width: 80%;
    border-radius: $border-radius;    
    padding: 1rem 2rem;
    color:$color-text;
    @include respond(tab){
        width: 100%;
    }
}

.tracker__container__input > input{
    text-align: center;
    font-size: 2rem;
    border-radius: $border-radius;
    padding: 1rem 2rem;
    margin-bottom: 2rem;
    color: #fff;
    background-color: transparent;
    border: 2px solid rgba($color-accent-green, 0.3);
    width: 100%;
}

.tracker__container__input > button:hover {
    background-color: rgba($color-accent-green, 0.7);
}

.tracker-expand-button{
    max-width: 30rem;
    text-align: center;
    font-size: 2rem;
    border-radius: $border-radius;
    padding: 1rem 2rem;
    margin-top: 2rem;
    color: #fff;
    background-color: transparent;
    border: 2px solid rgba($color-accent-green, 0.3);
}

.tracker-expand-button:hover {
    background-color: rgba($color-accent-green, 0.7);

}

.input{
    min-width: 15rem;
    @include respond(tab){
        width: 100%;
    }
    & > input, & > button {
        text-align: center;
        font-size: 2rem;
        border-radius: $border-radius;
        padding: 1rem 2rem;
        color: $color-text;
        background-color: transparent;
        border: 2px solid rgba($color-accent-green, 0.3);
        width: 100%;
        @include respond(tab){
            padding: 2rem 4rem;
        }
        @include respond(phone){
            padding: 1rem 2rem;
        }
    }

    & > button:not(:last-child) {
        margin-bottom: 2rem;

    }

    & > .notice-me{
        background-color: rgba($color-accent-green, 0.7);
    }

    & > button:hover {
        background-color: rgba($color-accent-green, 0.7);

    }

    &-select {
        min-width: 10rem;
        text-align: center;
        font-size: 2rem;
        border-radius: $border-radius;
        color: $color-text;
        background-color: transparent;
        border: 2px solid rgba($color-accent-green, 0.3);
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-orient: horizontal;
        -webkit-box-direction: normal;
            -ms-flex-direction: row;
                flex-direction: row;
        @include respond(tab){
            width: 100%;
        }
        & > input{
            text-align: center;
            font-size: 2rem;
            padding: 1rem 2rem 1rem 0;
            color: $color-text;
            background-color: transparent;
            border: none;
            width: 70%;
            @include respond(tab){
                padding: 2rem 2rem 2rem 0;
                width: 70%;

            }
            @include respond(phone){
                padding: 1rem 2rem 1rem 0;
            }
        }

        & > select {
            text-align: center;
            font-size: 2rem;
            padding: 1rem 0 1rem 0;
            color: $color-text;
            background-color: transparent;
            border: none;
            width: 20%;
            -webkit-appearance:none;
            min-width: 0;
            text-indent: 35%;

            @include respond(tab){
                padding: 2rem 0 2rem 0;
                width: 30%;
            }
            @include respond(phone){
                padding: 1rem 2rem 1rem 0;
            }
        }
    }
}

select {
    border: 2px solid rgba($color-accent-green, 0.3);
    border-radius: $border-radius;
    text-indent: 35%;
    font-size: 2rem;
    padding: 1rem 0 1rem 0;
    color: $color-text;
    background-color: transparent;
    width: 20%;
    -webkit-appearance:none;
    min-width: 15rem;
    @include respond(tab){
        text-indent: 37%;
    }
}