table{
    table-layout: fixed;
    width: 100%;
    border-collapse: collapse;
    background-color: $color-base-inner;
    color: $color-text;
}

.table{
    &__transactions{
        width: 50%;
        min-width: 15rem;
    }
    &__data__header{
        border-bottom: 1px solid rgba($color-accent-green,.8);
    }
}

th{
    font-weight: bold;
    font-size: 1.2em;
}

td{
    overflow:scroll;

    &, th {
        padding: 1rem 0;
        text-align: center;
    }
}

tr {
    border-bottom: 1px solid rgba($color-accent-green,.8);
    width:100%;
    border-color: transparent;

    & > td {
        overflow: scroll;
        @include respond(tab){
            font-size: 2rem;
        }
        @include respond(phone){
            font-size: 1.5rem;

        }
        & > a {
            width: 100%;
            height: 100%;
            color: $color-text;
            @include respond(tab){
                font-size: 2rem;
            }
            @include respond(phone){
                font-size: 1.5rem;
    
            }
        }
    }
    &:last-child td{
        border-bottom: none;

    }
}

select:focus, input:focus{
    outline: none;
}

.table__subtext {
    color: #ccc;
    font-size: 1rem;
}

.expand-button {
    text-align: center;
    font-size: 1.5rem;
    border-radius: $border-radius;
    color: $color-text;
    padding: 1rem 2rem;
    background-color: transparent;
    border: 2px solid rgba($color-accent-green, 0.3);
    width: 80%;
    cursor: pointer;
}

.expand-button:hover {
    background-color: rgba($color-accent-green, 0.7);

}

.expanded__row {
    -webkit-animation: fadeInAndExpand 2s;
            animation: fadeInAndExpand 2s;
}

.Passive {
    background-color: rgba($color-accent-green, .4);
}
.Negativ {
    background-color: rgba($color-accent-purple, .8);
    &__text{
        color: rgba($color-accent-purple, 1);
    }
}

.Positiv {
    background-color: rgba($color-accent-green, .8);

    &__text{
        color: rgba($color-accent-green, 1);
    }
}

.transactions{
    display:-webkit-box;
    display:-ms-flexbox;
    display:flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}


.narrow__whales{
    width:20%;
}

.very__narrow__whales{
    width:10%;
}

.normal{
    width:25%;
}
@-webkit-keyframes fadeInAndExpand {
    0% {
        opacity: 0;
        height: 0;
        overflow: hidden;
    }
    100% {
        opacity: 1;
        height: 100%;
        overflow: auto;
    }
}

@keyframes fadeInAndExpand {
    0% {
        opacity: 0;
        height: 0;
        overflow: hidden;
    }
    100% {
        opacity: 1;
        height: 100%;
        overflow: auto;
    }
}

@media (max-width: 37.5em) {
    th, td {
        font-size: 1.2rem;
    }

    .expand-button {
        font-size: 1.3rem;
        padding: .5rem 1rem;

    }
}